.header {
  padding: 5px;
  background-color: rgba(9, 46, 41, 0.788);
  color: whitesmoke;
  font-family: "Courier New", Courier, monospace;
  font-size: 30px;
  border-radius: 20px;
}

.book_form {
  margin: 20px 0 0 0;
  padding: 10px;
  border: solid 3px;
  border-radius: 8px;
  flex-direction: column;
  display: flex;
  height: auto;
  justify-content: space-around;
  text-align: center;
}

.table {
  height: auto;
}

.row {
  display: flex;
  justify-content: space-evenly;
  margin: 20px 0 0 0;
  /* width: 100%; */
}
.phone-phone-headermedia {
  background-color: black;
  box-shadow: 5px 0 0 0px;
  margin-bottom: 50px;
  margin-top: 20px;
  color: white;
  padding: 12px;
  border-radius: 7px;
  text-align: left;
}

.phone-phone-socialmedia a {
  margin-left: 10px;
  color: white;
  font-size: 18px;
  margin-top: -10px;
  margin-bottom: 10px;
}

.phone-phone-socialmedia a:hover {
  color: yellow;
}

/*  */
@media only screen and (max-width: 274px) {
  .row {
    flex-direction: column;
  }
}

.row input {
  border: solid black thin;
  padding: 4px;
  outline: none;
}

.input {
  border: solid black thin;
  padding: 4px;
  outline: none;
}

.button {
  border: solid thin;
  padding: 5px;
  background: transparent;
  margin: 10px 0 0 0;
}

.button:hover {
  background-color: blue;
  color: white;
  cursor: pointer;
}
